import React, { Component } from 'react'
import { AppInput, AppModal } from 'components/common-ui'

export default class RoomForm extends Component {

  constructor(props){
    super(props)
    this.state = {
      room_name:''
    }
  }

  edit = (e)=>{
    this.setState({[e.target.name]:e.target.value})
  }

  submit = ()=>{
    this.props.submit(this.state)
  }

  render(){
    const { room_name } = this.state
    const { edit, submit } = this
    return (
      <AppModal id="room-form" label={<i className="fas fa-plus"></i>}>
        <div className='row'>
          <div className='col-9'>
            <AppInput name='room_name' label='Tn phng' value={room_name} onChange={edit}/>
          </div>
          <div className='col-3 text-right'>
            <button className='btn btn-primary' onClick={submit}>To</button>
          </div>
        </div>
      </AppModal>
    )
  }

}


