import React, { Component } from 'react'
import Section from './section'
import { isRole } from 'ulti'

export default class Dashboard extends Component {

	render(){
	  	return (
	      <div className="row">
	        {isRole('admin','manager')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-pencil-alt'/>} label="HA N" url="#/order-creator"/></div>:null}
	        {isRole('admin','manager')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-clipboard'/>} label="PHNG" url="#/room"/></div>:null}
	        {isRole('admin','manager')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-cart-arrow-down'/>} label="BP" url="#/kitchen"/></div>:null}
	        {isRole('admin')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-folder-open'/>} label="DANH MC" url="#/category"/></div>:null}
	        {isRole('admin')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-file'/>} label="MT HNG" url="#/item"/></div>:null}
	        {isRole('admin')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-chart-line'/>} label="BO CO" url="#/report"/></div>:null}
	        {isRole('admin')?<div className="col-6 col-md-4"><Section icon={<i className='fas fa-user'/>} label="NHN VIN" url="#/user"/></div>:null}
	      </div>
	    )
	}

}


